"use client";

import React from "react";
import AnimatedContent from "../../components/AnimatedContent";
import ProjectCard from "../../components/ProjectCard";
import ClickSpark from "../../components/ClickSpark";
import SplitText from "../../components/SplitText";
import { projects } from "../../data/projects";

export default function ProjectsPage() {
  return (
    <ClickSpark sparkColor="#ffffff" sparkSize={10} sparkRadius={15} sparkCount={10} duration={450}>
      <main className="px-6 sm:px-10 py-10 mx-auto max-w-6xl">
      <section>
        <AnimatedContent distance={80} direction="vertical" duration={1.2} initialOpacity={0} animateOpacity>
          <div className="mb-8 sm:mb-10 text-center">
            <SplitText
              text="Projects"
              className="text-3xl sm:text-4xl md:text-5xl font-extrabold tracking-tight"
              delay={160}
              duration={1.2}
              ease="power3.out"
              splitType="chars"
              from={{ opacity: 0, y: 40 }}
              to={{ opacity: 1, y: 0 }}
              threshold={0.1}
              rootMargin="-100px"
              textAlign="center"
            />
          </div>
        </AnimatedContent>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {projects.map((p, idx) => (
            <AnimatedContent
              key={p.slug}
              distance={60}
              direction="vertical"
              duration={0.6}
              initialOpacity={0}
              animateOpacity
              delay={idx * 0.1}
            >
              <ProjectCard
                slug={p.slug}
                title={p.title}
                description={p.description}
                technologies={p.technologies}
                imageUrl={p.imageUrl}
                details={p.details}
                moreImages={p.moreImages}
                demoUrl={p.demoUrl}
                githubUrl={p.githubUrl}
                status={p.status}
              />
            </AnimatedContent>
          ))}
        </div>
      </section>
    </main>
    </ClickSpark>
  );
}
