"use client";

import React from "react";
import { useRouter } from "next/navigation";

interface ProjectCardProps {
  title: string;
  description: string;
  technologies: string[];
  imageUrl?: string;
  demoUrl?: string;
  githubUrl?: string;
  status?: "completed" | "in-progress" | "planned";
  details?: string;
  moreImages?: string[];
  slug: string;
}

const ProjectCard: React.FC<ProjectCardProps> = ({
  title,
  description,
  technologies,
  imageUrl,
  demoUrl,
  githubUrl,
  status = "completed",
  details: _details,
  moreImages: _moreImages = [],
  slug
}) => {
  const statusColors = {
    completed: "bg-green-500/10 text-green-400 border-green-500/20",
    "in-progress": "bg-yellow-500/10 text-yellow-400 border-yellow-500/20",
    planned: "bg-blue-500/10 text-blue-400 border-blue-500/20"
  };

  const statusLabels = {
    completed: "Completed",
    "in-progress": "In Progress",
    planned: "Planned"
  };

  const router = useRouter();
  const handleNavigate = () => router.push(`/projects/${slug}`);

  return (
    <div
      className="group relative bg-background/50 backdrop-blur-sm border border-foreground/10 rounded-xl p-6 hover:border-foreground/20 transition-all duration-300 hover:shadow-lg hover:shadow-foreground/5 cursor-pointer"
      role="button"
      tabIndex={0}
      onClick={handleNavigate}
      onKeyDown={(e) => {
        if (e.key === "Enter" || e.key === " ") {
          e.preventDefault();
          handleNavigate();
        }
      }}
      aria-label={`${title} details`}
    >
      {/* Status Badge */}
      <div className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium border mb-4 ${statusColors[status]}`}>
        {statusLabels[status]}
      </div>

      {/* Project Image */}
      {imageUrl && (
        <div className="relative mb-4 rounded-lg overflow-hidden bg-foreground/5">
          <img 
            src={imageUrl} 
            alt={title}
            className="w-full h-48 object-cover group-hover:scale-105 transition-transform duration-300"
          />
        </div>
      )}

      {/* Project Title */}
      <h3 className="text-xl font-bold mb-3 group-hover:text-foreground/90 transition-colors">
        {title}
      </h3>

      {/* Project Description */}
      <p className="text-foreground/70 mb-4 leading-relaxed">
        {description}
      </p>

      {/* Technologies */}
      <div className="flex flex-wrap gap-2 mb-6">
        {technologies.map((tech, index) => (
          <span 
            key={index}
            className="px-3 py-1 bg-foreground/5 text-foreground/80 rounded-full text-sm font-medium hover:bg-foreground/10 transition-colors"
          >
            {tech}
          </span>
        ))}
      </div>

      {/* Action Buttons */}
      <div className="flex gap-3">
        {demoUrl && (
          <a
            href={demoUrl}
            target="_blank"
            rel="noopener noreferrer"
            onClick={(e) => e.stopPropagation()}
            className="flex-1 bg-foreground text-background px-4 py-2 rounded-lg font-medium text-center hover:bg-foreground/90 transition-colors"
          >
            Live Demo
          </a>
        )}
        {githubUrl && (
          <a
            href={githubUrl}
            target="_blank"
            rel="noopener noreferrer"
            onClick={(e) => e.stopPropagation()}
            className="flex-1 border border-foreground/20 text-foreground px-4 py-2 rounded-lg font-medium text-center hover:border-foreground/40 hover:bg-foreground/5 transition-colors"
          >
            GitHub
          </a>
        )}
      </div>

      {/* Modal removed: navigates to detail page instead */}
    </div>
  );
}

export default ProjectCard;
